.navbar {
    background: transparent;
    height: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: fixed;
    margin-top: -20px;
    padding-top: 12px;
    width: 100%;
    z-index: 1;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    /* position: absolute; */
    height: 80px;
    max-width: 1500px;
    margin-top: 20px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    padding-bottom: 20px;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
    border:rgb(26, 23, 23)
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 60vh;
      position: absolute;
      top: 69px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      margin-top: 20px;
      padding-bottom: 70px;
      padding-right: 27px;
    }
  
    .nav-menu.active {
      background: #242222;
      margin-top: 20px;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
     
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
      padding-top: 7px;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }

  @media screen and (max-width: 450px) {
    .NavbarItems {
      position: relative;
    }

    .navbar-logo {
      position: absolute;
      /* top: 0;
      left: 0; */
      transform: translate(25%, 50%);
      font-size: 25px;
      padding-top: 7px;
      
    }

  
  }

  
  @media screen and (max-width: 350px) {
    .NavbarItems {
      position: relative;
    }

    .navbar-logo {
      position: absolute;
      /* top: 0;
      left: 0; */
      transform: translate(25%, 50%);
      font-size: 18.5px;
      padding-top: 14px;
      
    }

  
  }


  @media screen and (max-height: 670px) {
    .NavbarItems {
      position: relative;
    }

    .nav-menu {
     padding-bottom: 40px;
    }

}