.content_item_img {
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: auto;
  max-width: 100%;
  height: 100%;
  max-height: 350px;
  object-fit: cover;
  transition: all 0.2s linear;
  /* padding-bottom: 2500px; */
}



.content_item_img:hover {
  transform: scale(1.1);
}

.content_item_info {
  padding: 20px 30px 30px;
}

.content_item_text {
  color: white;
  font-size: 18px;
  line-height: 24px;
}

@media only screen and (min-width: 1200px) {
  .content_blog_container {
    width: 84%;
  }
}

